@import url(https://cdn.jsdelivr.net/gh/tonsky/FiraCode@4/distr/fira_code.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes colorPalette {
	0% {
		color:  cyan;
    }
	50% {
		color: magenta;
	}
	100% {
		color: lime;
	}
}

@keyframes colorPalette {
	0% {
		color:  cyan;
    }
	50% {
		color: magenta;
	}
	100% {
		color: lime;
	}
}

@-webkit-keyframes borderColorPalette {
	0% {
		border-color:  cyan;
    }
	50% {
		border-color: magenta;
	}
	100% {
		border-color: lime;
	}
}

@keyframes borderColorPalette {
	0% {
		border-color:  cyan;
    }
	50% {
		border-color: magenta;
	}
	100% {
		border-color: lime;
	}
}

.border-cycle {
  border: 2px;
  -webkit-animation-name: borderColorPalette;
          animation-name: borderColorPalette;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.color-cycle {
  color: cyan;
  -webkit-animation-name: colorPalette;
          animation-name: colorPalette;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

* {
  color: white;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

*:not(i):not(.fa) {
  font-family: 'Fira Code', monospace;
}

*::-webkit-scrollbar {
  display: none;
}

.black {
  background-color: black;
}

.dark-background {
  background-color: black;
  padding: 1%;
  border-right-style: solid;
  border-left: solid;
  border-color: cyan;
}

.max {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}

.lime-text {
  color: lime;
}

.magenta-text {
  color: magenta;
}

.cyan-text {
  color: cyan;
}

.gray-text {
  color: gray;
}

.red-text {
  color: red;
}

.blue-text {
  color: blue;
}

.teal-text {
  color: teal;
}

a {
  text-decoration: none;
}

li {
  font-size: 1.5em;
}
@-webkit-keyframes borderColorPalette {
	0% {
		border-color:  cyan;
    }
	50% {
		border-color: magenta;
	}
	100% {
		border-color: lime;
	}
}

@keyframes borderColorPalette {
	0% {
		border-color:  cyan;
    }
	50% {
		border-color: magenta;
	}
	100% {
		border-color: lime;
	}
}

.thumb {
    height: 10em;
    width: 15em;
    background-color: #121212;
    transition: transform .2s;
    text-align: center;
    border-style: solid;
    border-width: 1px;
    border-radius: 15px;
    padding-top: 25%;
    text-decoration: none;
    -webkit-animation-name: borderColorPalette;
            animation-name: borderColorPalette;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.thumb:hover {
    transform: scale(1.2); 
    text-decoration: none;
}

.big-thumb {
    height: auto;
    width: 100%;
    background-color: #121212;
    transition: transform .2s;
    border-style: solid;
    border-width: 1px;
    border-radius: 15px;
    padding: 3%;
    text-decoration: none;
    -webkit-animation-name: borderColorPalette;
            animation-name: borderColorPalette;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}
